	module.exports = {"data":{"wordpressWpLessons":{"id":"404f6b7c-361f-5a16-bf9a-3f3cc74052f7","wordpress_id":87,"title":"Effective Email","slug":"effective-email","content":"<h2>Goals and Overview</h2>\n<p>Establishing a regular email routine is very important in this digital age. You will be using email in this program and probably for a long time into your professional career. We will be communicating with you via email often. Knowing some best practices for crafting effective email will go a long way for you and today we are going to help you get started with this.</p>\n<h3>Goals:</h3>\n<ol>\n<li>Log in to and set up your DHFTC email address</li>\n<li>Demonstrate an ability to craft an effective email</li>\n</ol>\n<hr />\n<h2>Setting Up Your Email</h2>\n<p>As a good Digital Citizen, it is important to be able to communicate effectively through email. We will be using email to communicate with you since we are setting you up with an email address that is all your own! You will be expected to use email for the majority of your professional career, no reason for you not to start today!</p>\n<h2 class=\"learn-activity\">Activity: Setup Your DHF Email</h2>\n<ol>\n<li>Login to your NEW Gmail account!\n<ul>\n<li>Go to <a href=\"http://www.gmail.com\" target=\"_blank\" rel=\"noopener\">www.gmail.com</a><br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/MF_-_Digital_Communication_-_Effective_Email_-_gmail_1-600px2.jpg\" alt=\"Gmail Step 1\" /></li>\n<li>Click the <em>Sign in</em> button</li>\n<li>Enter your Username: First Initial Last Name <strong><span style=\"color: #0091c9;\">@youth.dhftc.org</span></strong> &#8211; example: <strong>sgrimes@youth.dhftc.org</strong><br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/MF_-_Digital_Communication_-_Effective_Email_-_gmail_2-600px3.jpg\" alt=\"Gmail Step 2\" />&#8220;</li>\n<li>If it appears, click the <em>Next</em> button. Otherwise, move to the next step!</li>\n<li>Ask a Staff Member for your password, then enter your password:<br />\n<img src=\"http://dhf-website.s3.amazonaws.com/images/MF%20-%20Digital%20Communication%20-%20Effective%20Email%20-%20gmail%203-600px1.jpg\" alt=\"Gmail step 3\" /></li>\n<li><a href=\"https://support.google.com/mail/answer/41078?hl=en&amp;ref_topic=3394465&amp;rd=1\" target=\"_blank\" rel=\"noopener\">Change your password</a>. Use a password you will remember.</li>\n</ul>\n</li>\n<li><a href=\"https://support.google.com/mail/answer/8395?hl=en&amp;ref_topic=3394466\" target=\"_blank\" rel=\"noopener\">Set your Email Signature</a>. This should include your First and Last Name and could include a link to your website or some other way to show your personal brand. <strong>REMEMBER</strong>: This is professional communication, keep it appropriate for all audiences.</li>\n<li><a href=\"https://support.google.com/mail/answer/35529?hl=en&amp;ref_topic=3394466\" target=\"_blank\" rel=\"noopener\">Select a photo to use with your email</a>. You can do this later, once you receive your Tech Center photo from us.</li>\n<li><a href=\"https://support.google.com/mail/answer/8933?hl=en&amp;ref_topic=3396990\" target=\"_blank\" rel=\"noopener\">Add your first Contacts</a>. Your first contacts will be Groups. Using an email group is a great way to include all members of a group without sending multiple emails. Instead, you send to one email address and every person in the Group receives the message.</li>\n<li><strong>Task</strong>: Add each of the following Groups to your email as new contacts:\n<ul>\n<li><strong>programs@digitalharbor.org</strong> (Program Staff)</li>\n<li><strong>makerfoundations@digitalharbor.org</strong> (All Participants)</li>\n</ul>\n</li>\n</ol>\n<h2>Sending Email</h2>\n<p>When sending emails, there is a certain etiquette that you should try to follow. Paying close attention to who you are including on emails you send and giving your emails relevant titles are two really important etiquette pieces you need to remember.</p>\n<h2><span style=\"color: #0091c9;\">Activity: Good Email Etiquette</span></h2>\n<ol>\n<li>Read the following post about the differences between the To, CC and BCC fields when sending an email: <a href=\"http://www.writebetteremails.com/to-cc.htm\" target=\"_blank\" rel=\"noopener\">To, CC, and BCC</a></li>\n<li>Read a little about what makes a good <a href=\"http://www.writebetteremails.com/subject.htm\" target=\"_blank\" rel=\"noopener\">Subject Line</a>.</li>\n<li>Understand what types of things to send as attachments to email and how to do send attachments.\n<ul>\n<li><a href=\"http://www.writebetteremails.com/attachments.htm\" target=\"_blank\" rel=\"noopener\">Types of Email Attachments</a></li>\n<li><a href=\"https://support.google.com/mail/answer/6584?hl=en&amp;ref_topic=3396261\" target=\"_blank\" rel=\"noopener\">Gmail Help: Attachments</a></li>\n</ul>\n</li>\n<li><strong>Send your first two emails from your new DHF account</strong>\n<ul>\n<li>Email <span style=\"color: #0091c9;\"><strong>megalab</strong><strong>@digitalharbor.org</strong></span> to introduce yourself to the Directors and answer these questions:\n<ul>\n<li><em>What questions do you have? What are your expectations for the program? What do you hope to get out of Maker Foundations?</em></li>\n</ul>\n</li>\n<li>Email <strong><span style=\"color: #0091c9;\">makerfoundations@digitalharbor.org</span></strong> and introduce yourself, tell something you enjoy (technology based or not) and share with them something you are interested in learning in Maker Foundations. Attach a photo of yourself if you have one.</li>\n</ul>\n</li>\n</ol>\n<p>[box type=&#8221;info&#8221;]Before marking this lesson as complete, make sure you&#8217;ve sent the Activity emails![/box]</p>\n<h2>Additional Resources</h2>\n<ul>\n<li><a href=\"https://mail.google.com\" target=\"_blank\" rel=\"noopener\">Gmail</a></li>\n<li><a href=\"https://support.google.com/mail/#topic=3394144\" target=\"_blank\" rel=\"noopener\">Gmail Help Center</a></li>\n<li><a href=\"http://www.inc.com/guides/2010/06/email-etiquette.html\" target=\"_blank\" rel=\"noopener\">25 Tips for Perfecting Your Email Etiquette</a></li>\n<li><a href=\"http://www.writebetteremails.com/index.php\" target=\"_blank\" rel=\"noopener\">Write Better Emails</a></li>\n<li><a href=\"http://www.howtogeek.com/128028/htg-explains-whats-the-difference-between-cc-and-bcc-when-sending-an-email/\" target=\"_blank\" rel=\"noopener\">Difference Between CC &amp; BCC</a></li>\n</ul>\n"}},"pathContext":{}}

